@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
}
html {
  width: 100%;
  height: 100%;
}
ul {
  list-style: none;
}
body {
  font-size: 14px !important;
  background-color: #f2f2f2;
  font-family: 微软雅黑;
  width: 100%;
  height: 100%;
}
span {
  border: none;
}
input {
  outline: none;
}
a {
  text-decoration: none;
  color: #000000;
}
button {
  background: none;
  border: none;
}
#app {
  padding-top: 2.13333333rem;
  background: #FFFFFF;
  height: 100%;
  /*    text-align: center;
      color: #2c3e50; */
}
a {
  text-decoration: none;
}
header {
  display: -webkit-flex;
  display: flex;
  line-height: 2.13333333rem;
  border-bottom: 1px solid #f2f2f2;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background: #FFFFFF;
}
header > a {
  width: 7%;
  font-size: 20px;
  text-align: center;
}
header img {
  width: 100%;
  vertical-align: middle;
}
header div:nth-child(1) {
  width: auto;
}
header div:nth-child(2) {
  width: 90%;
  text-align: center;
}
header div:nth-child(3) {
  width: 5%;
  text-align: right;
}
#container {
  width: 100%;
  height: 100%;
  font-size: 15px;
}
#panel {
  background-color: white;
  max-height: 90%;
  overflow-y: auto;
  top: 10px;
  right: 10px;
  width: 280px;
  border-bottom: solid 1px silver;
}
.footer_map_buttons {
  background: #FFFFFF;
  width: 100%;
  font-size: 0;
}
.footer_map_buttons button {
  font-size: 15px;
  background: #FFFFFF;
  border: none;
  width: 24%;
  display: inline-block;
  line-height: 2.66666667rem;
}
.footer_map_buttons .enroll {
  display: inline-block;
  width: 28%;
  background: #fe4a4a;
  color: #FFFFFF;
}
.footer_map_buttons .active {
  color: #fe4a4a;
  /* border-top: 1px solid #fe4a4a; */
}
.content {
  position: absolute;
  background: #FFFFFF;
  left: 0;
  top: 0;
  width: 100%;
  height: 50%;
  visibility: hidden;
  z-index: 9999;
}
.content > .content-top {
  height: 84%;
  overflow: hidden;
}
.content > .content-top > div {
  font-size: 12px;
  line-height: 1.86666667rem;
  border-bottom: 1px solid #aeaeae;
  margin: 0 5%;
  display: -webkit-flex;
  display: flex;
  justify-content: space-between;
}
.content > .content-bottom {
  height: 10%;
  text-align: center;
  font-weight: 300;
  color: #333;
  font-size: 0.8rem;
}
.list-button {
  font-size: 12px;
  position: absolute;
  background: #FFFFFF;
  left: 1%;
  bottom: 8%;
  width: 20%;
  padding: 2% 0;
  height: 6%;
  z-index: 200;
}
.list-button > div {
  text-align: center;
}
h3 {
  width: 100%;
}
#app > div {
  position: absolute;
  padding: 0;
  width: 100%;
  background: #FFFFFF;
}



